<template>
  <div class="session-page">
    <!-- Page Header -->
    <h2 class="header-title">üìÖ Upcoming Group Sessions</h2>

    <!-- Session Cards -->
    <div class="session-grid">
      <div
        v-for="session in sessions"
        :key="session.id"
        class="session-card"
      >
        <div class="card-header">
          üß† {{ session.topic }}
        </div>
        <div class="card-time">
          üïí {{ formatTime(session.time) }}
        </div>
        <button class="join-btn" @click="joinSession(session)">
          Join Session
        </button>
      </div>
    </div>

    <!-- Create Session Form -->
    <div class="form-container">
      <h3 class="form-title">‚ûï Create New Session</h3>
      <form @submit.prevent="createSession" class="form-fields">
        <div>
          <label class="form-label">Session Topic</label>
          <input
            v-model="topic"
            placeholder="e.g., AI Study Group"
            required
            class="form-input"
          />
        </div>

        <div>
          <label class="form-label">Date & Time</label>
          <input
            v-model="time"
            type="datetime-local"
            required
            class="form-input"
          />
        </div>

        <button type="submit" class="create-btn">
          ‚úÖ Create Session
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      topic: '',
      time: '',
      sessions: []
    };
  },
  mounted() {
    this.fetchSessions();
  },
  methods: {
    formatTime(datetimeStr) {
      return new Date(datetimeStr).toLocaleString();
    },

    async fetchSessions() {
      try {
        const token = localStorage.getItem('token');
        const res = await fetch('/session/all', {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });

        if (!res.ok) throw new Error('Failed to fetch sessions');
        this.sessions = await res.json();
      } catch (err) {
        console.error('‚ùå Fetch Error:', err.message);
      }
    },

    async createSession() {
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          alert('‚ùå You must be logged in to create a session');
          return;
        }

        const response = await fetch('/session/create', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`
          },
          body: JSON.stringify({
            topic: this.topic,
            time: this.time
          })
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || 'Unknown error');
        }

        const result = await response.json();
        alert(`‚úÖ Session created: "${result.topic}" at ${this.formatTime(result.time)}`);

        this.topic = '';
        this.time = '';
        this.fetchSessions();
      } catch (err) {
        alert(`‚ùå Error: ${err.message}`);
        console.error(err);
      }
    },

    joinSession(session) {
      alert(`üöÄ Joining "${session.topic}" at ${this.formatTime(session.time)} üé•`);
      // Connect to real room in future
    }
  }
};
</script>

<style scoped>
.session-page {
  padding: 2rem;
  max-width: 960px;
  margin: 0 auto;
  font-family: 'Segoe UI', sans-serif;
}

.header-title {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  background: linear-gradient(to right, #3b82f6, #6366f1);
  color: white;
  padding: 0.8rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.session-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin-bottom: 2.5rem;
}

.session-card {
  background-color: #fff;
  border: 2px solid #e0e7ff;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.session-card:hover {
  transform: translateY(-4px);
  border-color: #6366f1;
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.2);
}

.card-header {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e40af;
  margin-bottom: 0.5rem;
}

.card-time {
  font-size: 0.95rem;
  color: #6b7280;
  margin-bottom: 1rem;
}

.join-btn {
  align-self: flex-start;
  background-color: #10b981;
  color: white;
  padding: 0.5rem 1.2rem;
  border-radius: 0.5rem;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.join-btn:hover {
  background-color: #059669;
}

.form-container {
  background: linear-gradient(to bottom right, #f9fafb, #e0f2fe);
  padding: 2rem;
  border-radius: 1.25rem;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.08);
  border: 1px solid #d1d5db;
}

.form-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  color: #1d4ed8;
  text-align: center;
}

.form-fields {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.form-label {
  font-size: 0.9rem;
  color: #374151;
  margin-bottom: 0.4rem;
}

.form-input {
  width: 100%;
  padding: 0.6rem 0.8rem;
  border: 1px solid #cbd5e1;
